.forecast_day_wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    flex-wrap: wrap;
    width: 200px;
    min-height: 350px;
    border: none;
    padding: 5px 10px;
    margin: 5px;
    border-radius: 10px;



}

.forecast_day_wrapper:hover {
    margin-bottom: 10px;
    margin-top: -10px;
    border-radius: 10px;
    box-shadow:rgba(0, 0, 0, 0.12) 0px 8px 16px 0px, rgba(0, 0, 0, 0.08) 0px 0px 1px 1px;
    box-sizing:border-box;
    color:rgb(34, 36, 38);
    transition-delay:0s, 0s;
    transition-duration:0.16s, 0.16s;
    transition-property:box-shadow, -webkit-box-shadow;
    transition-timing-function:cubic-bezier(0.25, 0.1, 0.25, 1), cubic-bezier(0.25, 0.1, 0.25, 1);
}

.freezy {
    background: #00B4DB;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #0083B0, #00B4DB);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #0083B0, #00B4DB); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.cool {
    background: #43cea2;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #185a9d, #43cea2);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #185a9d, #43cea2); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.warm {
    background: #FFE000;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #799F0C, #FFE000);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #799F0C, #FFE000); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.hot {
    background: #e65c00;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #F9D423, #e65c00);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #F9D423, #e65c00); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.day_info {
    width: 100%;
    margin-top: 10px;
}

.day_info_row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.fc_day__title {
    font-size: 18px;
    color: rgba(0, 0, 0, 0.8);
}
.fc_day_part__title {
    text-transform: capitalize;
    color: #ffffff;
    width: 15%;
}

.fc_info_title {
    color: rgba(0, 0, 0, 0.8);
    font-size: 15px;
    font-weight: 600;
}
.fc_info_value {
    color: rgba(0, 0, 0, 0.8);
    font-size: 15px;
    font-weight: 600;
}
.fc_day_part__weather {
    width: 25%;
}


.fc_temp_day {
    color: #4e5055;
    font-size: 25px;
}

.fc_temp_night {
    color: #939cb0;
    font-size: 20px;
}

.fc_dayparts_wrapper {
    border-top: 1px thin #4e5055;
}